<template>
    <el-calendar>
      <template #date-cell="{ data }">
        <div v-for="course in 返回单元格数据对象(data.day)" :key="course">
            {{ course.course_name +" " + course.classroom + " 第"+ course.lesson + "节" }}
        </div>
      </template>
    </el-calendar>
  </template>


<script>
import student_api from '@/api/student/student-api'
import student_function from '@/api/student/student-function'
export default{
    data(){
        return{
            user_id:"",
            timetable:{},
        }
    },
    mounted() {
    this.user_id = this.$route.query.user_id
    student_function.身份校验检查(this,this.user_id)

    student_api.获取课程表({user_id:this.user_id}).then(resp=>{
        if(resp.status_code == 200){
            this.timetable = resp.data
        }
        else{
            student_function.Alert_Error(this,resp.data)
        }
    })
  },
  methods:{
    返回单元格文本(target) {
        let courses = []
        let text = ""
        courses = this.timetable[target]
        if(courses == null){
            return ''
        }
        courses.forEach(course=>{
            text += course.course_name + " ";
            text += course.classroom + " 第";
            text += course.lesson + "节 ";
        })
        return text
    },
    返回单元格数据对象(target){
        let courses = this.timetable[target]
        if(courses == null){
            return null
        }
        return courses
    }
  }

}


</script>

